import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);

const state = {
  //state
  showFooter: true,
  changableNum: 0,
  pPage: false,
  cPage: false,
  songInfo: "",
  playInfo: {}, // id:'',name:'',bgurl:'',audio:''
  audio: "",
  playing: false,
  zColor: "",
  songList: []
};
const getters = {
  //state()
  isShow(state) {
    //,showFooter
    return state.showFooter;
  },
  getChangedNum() {
    //,changableNum
    return state.changebleNum;
  }
};
const mutations = {
  toggleC(state) {
    //statestate();
    state.cPage = !state.cPage;
  },
  togglePlay(state) {
    if (state.audio != "") {
      state.pPage = !state.pPage;
    }
  },
  newNum(state, sum) {
    //statesum
    state.changableNum += sum;
  }
};
const store = new Vuex.Store({
  state,
  getters,
  mutations
});
export default store;



